* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont,'Roboto', 'Segoe UI',  'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  max-width: 430px;
  margin: 0 auto;
}

.Welcome {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 20vh;
  margin-top: 30%;
  font-family: 'Poppins';
}

main {
  min-height: 100vh;
  /* background-color: rgb(235,198,178); */
  padding-top: 50px;
}


.search-box {
  /* margin-top: 75px; */
  width: 100%;
}

.search-box input {
  display: block;
  font-family: 'Roboto';
  font-weight: 300;
  margin: 0 auto;
  width: 70%;
  border: 0;
  border-bottom: 2px solid rgba(0, 0, 0, 50%);
  outline: 0;
  font-size: 1.3rem;
  color: black;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
}

.search-box input::placeholder {
  color: rgba(0, 0, 0, 50%);
}

.search-box-night input {
  display: block;
  font-family: 'Roboto';
  font-weight: 300;
  margin: 0 auto;
  width: 70%;
  border: 0;
  border-bottom: 2px solid white;
  outline: 0;
  font-size: 1.3rem;
  color: white;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
}

.search-box-night input::placeholder {
  color: white;
}

.image-box {
  width: 70%;
  margin: 0 auto;
}

img {
  width: 100%;
}

.weather-info {
  width: 90%;
  margin: 0 auto;
}

.temp {
  font-family: 'Poppins';
  width: 90%;
  display: flex;
  margin: 0 auto;
}

.temp-normal {
  margin: 0 auto;
  font-size: 6rem;
  padding-left: 35px;
}

.temp-min,
.temp-max {
  align-self: flex-end;
  display: flex;
  align-items: center;
  font-family: 'Roboto';
  height: 20px;
  width: 20px;
}

.temp-min img {
  transform: rotate(180deg);
  height: 60%;
  fill: white;
}

.temp-max img {
  height: 60%;
  margin-left: -20px;
}

.location-date-box {
  width: 100%;
  font-family: 'Roboto';
  font-weight: 300;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
}

.location {
  font-size: 2rem;
}

.date {
  margin-top: 5px;
  font-style: italic;
}

.time-box {
  display: flex;
  justify-content: center;
  margin: 30px 0;
  align-items: center;
  font-size: 1.5rem;
  font-family: 'Roboto';
  font-weight: 300;
}
